<template>
	<!-- <div> -->
		
		<div class="container mt-4">
            <ButtonResetApp v-on:resetApp="load" />
			<div class="row  mb-2">
				<div class="col-md-1">
					<button
						type="button"
						class="btn btn-primary"
						@click="showModal('create')"
					>
						Agregar
					</button>
				</div>
			</div>

			<!-- MODAL -->
			<div class="modal fade" tabindex="-1" ref="modal">
				<div class="modal-dialog modal-dialog-centered">
					<div class="modal-content">
						<div class="modal-header">
							<h5 class="modal-title">{{ modalTitle }}</h5>
							<button
								type="button"
								class="btn-close"
								data-bs-dismiss="modal"
								aria-label="Close"
							></button>
						</div>
						<div class="modal-body">
							<form ref="form">
								<div class="form-group">
									<label for="first_name">First name:</label>
									<input
										type="text"
										id="first_name"
										v-model="first_name"
										class="form-control"
									/>
								</div>
								<div class="form-group">
									<label for="last_name">Last name:</label>
									<input
										type="text"
										id="last_name"
										v-model="last_name"
										class="form-control"
									/>
								</div>
								<div class="form-group">
									<label for="email">Email:</label>
									<input
										type="email"
										id="email"
										v-model="email"
										class="form-control"
									/>
								</div>
								<div class="form-group">
									<label for="work">Work:</label>
									<input
										type="text"
										id="work"
										v-model="work"
										class="form-control"
									/>
								</div>
							</form>
						</div>
						<div class="modal-footer">
							<button
								type="button"
								class="btn btn-secondary"
                                @click="hideModal"
							>
								Close
							</button>
							<button
								type="button"
								class="btn btn-primary"
								@click="createUser()"
								v-if="mode === 'create'"
							>
								Save changes
							</button>
							<button
								type="button"
								class="btn btn-primary"
								@click="updateUser()"
								v-if="mode === 'update'"
							>
								Update
							</button>
						</div>
					</div>
				</div>
			</div>

            <div class="row justify-content-center me-1 mb-2">
                <div class="col-md-12">
                    <input type="text" @keyup="filterUser()" class="form-control" v-model="filter" placeholder="Buscar...">
                </div>
            </div>

			<div class="row row-cols-1 row-cols-md-3 row-cols-lg-4 g-4 me-0">
				<div class="col" v-for="user in users" :key="user.id">
					<div class="card">
						<img
							:src="user.avatar || defaultimage"
							class="card-img-top"
						/>
						<div class="card-body">
							<h5 class="card-title">
								{{ user.first_name + " " + user.last_name }}
							</h5>
                            <span class="text-muted">{{user.work}}</span>
							<p class="card-text">
								{{ user.email }}
							</p>
						</div>
						<div class="card-footer">
							<button
								class="btn btn-sm btn-secondary me-1"
								@click="loadFormAndShow(user)"
							>
								Editar
							</button>
							<button
								class="btn btn-sm btn-danger "
								@click="deleteUser(user.id)"
							>
								Eliminar
							</button>
						</div>
					</div>
				</div>
			</div>
		</div>
	<!-- </div> -->
</template>

<script>
	import UserService from "../services/User";
	import Modal from "bootstrap/js/dist/modal";
	import * as defaultimage from "@/assets/user.png";
	import ButtonResetApp from "./ButtonResetApp.vue";
	export default {
		name: "UserList",
		components: { ButtonResetApp },
		data() {
			return {
				users: [],
				works: [
					"web developer",
					"desktop developer",
					"mobile developer",
					"graphic designer",
					"tester",
					"game designer",
				],
				id: null,
				first_name: "",
				last_name: "",
				email: "",
				work: "",
				avatar: "",
				modal: {},
				modalTitle: "",
				defaultimage: defaultimage,
				mode: "create",
                filter: ""
			};
		},
		methods: {
			async load() {
				if (!localStorage.getItem("userList")) {
					const data = await UserService.getAll();
					if (data && typeof data !== "object") {
						alert(data);
						return;
					}

					//Le asigno un trabajo alatorio a cada user
					data.data.forEach((user) => {
						user.work =	this.works[this.getRandomInt(0, data.data.length)];
					});

                    this.users =  data.data
					localStorage.setItem("userList", JSON.stringify(this.users));
				} else {
					this.users = JSON.parse(localStorage.getItem("userList"));
				}
			},

			createUser() {
				let userObject = {
					first_name: this.first_name,
					last_name: this.last_name,
					work: this.work,
					avatar: "",
					email: this.email,
				};
				UserService.createUser(userObject);
				this.load();
				this.hideModal();
			},

			updateUser() {
				let userObject = {
					id: this.id,
					first_name: this.first_name,
					last_name: this.last_name,
					work: this.work,
					avatar: "",
					email: this.email,
				};
				this.users = UserService.updateUser(userObject);
				this.hideModal();
			},

			deleteUser(userId) {
				this.users = UserService.deleteUser(userId);
			},

			loadFormAndShow(user) {
				this.first_name = user.first_name;
				this.last_name = user.last_name;
				this.work = user.work;
				this.email = user.email;
				this.id = user.id;
				this.showModal("update");
			},

			getRandomInt(min, max) {
				return Math.floor(Math.random() * (max - min)) + min;
			},

			showModal(mode) {
				if (mode == "create") {
					this.modalTitle = "Nuevo usuario";
					this.mode = "create";
				} else {
					this.modalTitle = "Editar usuario";
					this.mode = "update";
				}
				this.modal.show();
			},

			hideModal() {
				this.formReset();
				this.modal.hide();
			},

			formReset() {
				this.first_name = "";
				this.last_name = "";
				this.work = "";
				this.email = "";
			},

            filterUser(){

                console.log(this.filter)
                let userList = JSON.parse(localStorage.getItem('userList'))
                let value    = this.filter.toLowerCase()

                this.users = userList.filter(user => {
                    for (const key in user) {
                        if(
                            (user[key].toString().toLowerCase().indexOf(value) != -1) || 
                            ((user.first_name+' '+user.last_name).toLowerCase().indexOf(value) != -1)
                        ){
                            return user
                        }
                    }
                })
            }
		},

		mounted() {
			this.modal = new Modal(this.$refs.modal);
			this.load();
		},
	};
</script>

