const UserService = {
    getAll: async function(){
        try {
            const response = await fetch("https://reqres.in/api/users/")
            if(response && !response.ok){
                throw new Error("Error "+response.status)
            }else if(response){
                const data = await response.json()
                return data
            }
        } catch (error) {
            return error.message
        }
    },

    createUser: function(userObject){
        const users = JSON.parse(localStorage.getItem('userList'))
        userObject.id = this.nextId(users)
        users.push(userObject)
        localStorage.setItem('userList', JSON.stringify(users)) 
    },

    deleteUser: function(userId){
        const users = JSON.parse(localStorage.getItem('userList'))
        const index = users.findIndex(user => user.id == userId)
        users.splice(index, 1)
        localStorage.setItem('userList', JSON.stringify(users))
        return users
    },

    updateUser: function(userObject){
        
        let users = JSON.parse(localStorage.getItem('userList'))
        let usersUpdated = users.map(user => {
            if(user.id == userObject.id){
                user.first_name = userObject.first_name
                user.last_name  = userObject.last_name
                user.work = userObject.work
                user.email = userObject.email
            }

            return user
        })

        console.log(usersUpdated)
        localStorage.setItem('userList', JSON.stringify(usersUpdated))
        return usersUpdated
    },

    nextId: function(users){
        let ids = users.map(user => parseInt(user.id))

        return Math.max(...ids) + 1
    }
}

export default UserService