<template>
  <button class="position-absolute btn btn-dark" type="button" @click="resetApp()" >Reset App</button>
</template>

<script>
export default {
    name: 'ButtonResetApp',
    methods:{
        resetApp(){
            localStorage.removeItem('userList')
            this.$emit('resetApp')
        }
    }
}
</script>

<style scoped>
button{
    top:10px;
    right:10px;
}
</style>